import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"
import { getBalanceSheetFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function longtermInvestments(tableName: string) {
  const title = "Long-term Investments (FQ)"
  return genericColumn({
    endpoint: "companyBalanceStatement",
    columnKeys: ["longTermInvestments"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Investments held for more than a year.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyBalanceStatement}
          currency={info.row.original.financialData.companyBalanceStatement?.data?.reportedCurrency}
          onClick={() => {
            const fiscalPeriod = getBalanceSheetFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about long-term investments?`,
              value:
                info.row.original.financialData.companyBalanceStatement?.data?.longTermInvestments,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyBalanceStatement?.data?.longTermInvestments}
        />
      )
    },
  })
}

export { longtermInvestments }
